.navbar{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    width:100%;
    height: 44px;
    padding:0px 18px;
    

    border-top: 1px solid #dbdbdb;
    background-color: #fff;
}

.navItem{
    flex-basis: 20%;
    text-align: center;
}
