.header{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    box-sizing: border-box;
    position: fixed;
    top: 0;
    width:100%;
    height: 44px;
    padding:0px 18px;

    border-bottom: 1px solid #dbdbdb;
    background-color: #fff;
}

.headerItem{
    flex-basis: 33.33%;
    text-align: center;
}